<template>
    <div class="app-wrapper">
        <Header />
        <Recomended v-if="showRecomended" @editTest="editTest" />
        <GetReadySection v-if="showReady" @my-event="doSomething" />
        <ConditionTest v-if="showConditionTest" @my-event="doSomething" />
        <ConditionAssessment v-if="showCondition" @getTest="getTest" />
        <Footer />
    </div>
</template>

<script>
import Header from './Header.vue'
import Recomended from './Recomended.vue'
import GetReadySection from './GetReadySection.vue'
import ConditionTest from './ConditionTest.vue'
import ConditionAssessment from './ConditionAssessment.vue'
import Footer from './Footer.vue'


    export default {
        data() {
            return {
                showRecomended: false,
                showReady: true,
                showConditionTest: false,
                showCondition: true,
            }
        },
        methods: {
            getTest() {
                this.showReady = false
                this.showCondition = false
                this.showRecomended = true
                this.showConditionTest = true
            },
            editTest() {
                this.showReady = true
                this.showCondition = true
                this.showRecomended = false
                this.showConditionTest = false
            },
        },
        components: {
            Header,
            Recomended,
            GetReadySection,
            ConditionTest,
            ConditionAssessment,
            Footer
        }
    }
</script>

