                    value: 74,
                    price: 1400
                },
                {
                    name: 'Холестерин общий', 
                    value: 39,
                    price: 1400
                }
            ],
            percentColor: [],
            show: false,
        };
    },
    mounted() {
        location.href="#testResult"
    },
    computed: {
        filteredList() { 
            if (this.show === true) {
                return this.percent.filter((item) => {
                    return item.value > 60
                })
            } 
            return this.percent
            }
    },
    methods: {
        changeState() {
            this.show = !this.show;
        },
        selectClinics() {
            console.log('selectClinics');
        },

        checkPercentValue(value) {
            if (value >= 50 ) {
                 this.percentColor.push(' green');

            }
            else if ( value >= 31 && value <= 49 ) {
                this.percentColor.push('yellow');
                
            }
            else if ( value >= 16 && value <= 30 ) {
                this.percentColor.push('brown');
                
            }
            else if ( value <= 15 ) {
                this.percentColor.push('black');
                 
            }
            console.log(this.percentColor);
            return value
        }
    }
}

